/**
 * MotorCompanyContainer.js
 * The company container for motor.
 *
 * This is different form choose plan as it
 * just list all the avaliable insurers with no
 * pricings.
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getFormValues } from 'redux-form';

import PropTypes from 'prop-types';
import MotorCompany from '../../components/Motor/MotorCompany';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { clearMotorData, getMotorPlans, setSelectedInsurerIndex } from '../../actions/motorActions';
import { ampTag } from '../../lib/tagFunctions';

class MotorCompanyContainer extends Component {
  constructor(props) {
    super(props);

    const { plansData } = this.props;
    this.getNewData = this.getNewData.bind(this);

    if (!plansData) {
      this.getNewData();
    }
  }

  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.CHOOSECOMPANY.index);

    window.scrollTo(0, 0);
    ampTag('MotorCompanyS');
  }

  componentDidUpdate(prevProps) {
    const { plansData, loadingEnd } = this.props;
    if (!prevProps.plansData && plansData) {
      loadingEnd();
    }
  }

  getNewData() {
    const {
      motorForm,
      motorMoreForm,
      loading,
      clearData,
      history,
    } = this.props;

    const params = {
      ...motorForm,
      fwd_motor_quote_request: {
        plan_code: '',
        marital_status: motorMoreForm.marital_status,
        gender: motorMoreForm.gender,
        year_born: motorMoreForm.year_born.toString(),
        years_driving: motorMoreForm.years_driving,
        ncd: motorMoreForm.ncd.toString(),
        demerit_status: motorMoreForm.demerit_status,
        number_of_claims: motorMoreForm.number_of_claims,
        ...motorForm,
        year_registered: motorForm.year_registered.toString(),
        referral: '',
        promo_code: '',
      },
    };

    clearData();
    this.props.getMotorPlans(params);
    loading(() => {
      clearData();
      history.goBack();
    }, 'No available plans found.', 30000);
  }

  render() {
    const { plansData, motorFields, setInsurerIndex } = this.props;
    if (!plansData) {
      return null;
    }

    return (
      <MotorCompany
        plansData={plansData}
        motorFields={motorFields}
        setInsurerIndex={setInsurerIndex}
        submitCallback={this.getNewData}
      />
    );
  }
}

MotorCompanyContainer.defaultProps = {
  plansData: null,
};

MotorCompanyContainer.propTypes = {
  plansData: PropTypes.array,
  motorFields: PropTypes.object.isRequired,
  setInsurerIndex: PropTypes.func.isRequired,
  motorForm: PropTypes.object.isRequired,
  motorMoreForm: PropTypes.object.isRequired,
  loading: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
  getMotorPlans: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  clearData: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  plansData: state.motor.plansData,
  motorFields: state.motor.motorFields,
  motorForm: getFormValues('motor')(state),
  motorMoreForm: getFormValues('motor-more')(state),
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  getMotorPlans: (params) => {
    dispatch(getMotorPlans(params));
  },
  setInsurerIndex: (index) => {
    dispatch(setSelectedInsurerIndex(index));
  },
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
  clearData: () => {
    dispatch(clearMotorData());
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MotorCompanyContainer));
