/**
 * HeaderDescWithTabView.js
 * Product summary with tab view
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import PropTypes from 'prop-types';

import VoucherTag from 'components/Templates/VoucherTag/VoucherTag';
import TabView from 'components/Templates/TabView/TabView';
import Button from 'components/FormsV3/Button/Button';

import './HeaderDescWithTabView.scss';

class HeaderDescWithTabView extends Component {
  render() {
    const {
      logo,
      logoDesc,
      cost,
      unit,
      tabs,
      onClick,
      name,
      buttonSubText,
      teaser,
    } = this.props;

    return (
      <Col className="header-desc-with-tabview-container" xs={12}>
        <Row className="full-height" noGutters>
          <Col className="left-container d-none d-sm-flex">
            <div className="motor-company-container">
              <div className="company-logo-container">
                <img className="company-logo" src={logo} alt={`${logoDesc}.png`} />
              </div>
            </div>
          </Col>
          <Row className="right-main" noGutters>
            <Col xs={12} className="top-container">
              <div className="name-container">
                <div className="name-logo">
                  <div className="company-logo-container d-flex d-sm-none">
                    <img className="company-logo" src={logo} alt={`${logoDesc}.png`} />
                  </div>
                  <p className="desc">{logoDesc}</p>
                </div>
                <VoucherTag
                  teaser={teaser}
                  className="tag-label"
                />
              </div>
              <div className="price-container">
                <p>
                  <span className="cost">{cost}</span>
                  <span className="unit">{unit}</span>
                </p>
              </div>
            </Col>
            <Col className="mid-container">
              <TabView
                name={name}
                maxTabWidthPx={128}
                hideWidth={768}
                tabs={tabs}
                isDisableDropdown
                isConstantHeight
              >
                {this.props.children}
              </TabView>
            </Col>
            <Col className="right-container align-self-center">
              {/* {
                hidePrice ?
                  null
                :
                  <p>
                    <span className="cost">{cost}</span>
                    <span className="unit">{unit}</span>
                  </p>
              } */}
              <Button
                type="submit"
                variant="medium-flat-green"
                width={165}
                height={72}
                onClick={onClick}
              >
                <p>Choose Plan</p>
              </Button>
              {
                buttonSubText ?
                  <p className="button-sub-text">{buttonSubText}</p>
                :
                  null
              }
            </Col>
          </Row>
        </Row>
      </Col>
    );
  }
}

HeaderDescWithTabView.defaultProps = {
  cost: null,
  unit: null,
  onClick: () => {},
  buttonSubText: null,
  teaser: null,
};

HeaderDescWithTabView.propTypes = {
  logo: PropTypes.string.isRequired,
  logoDesc: PropTypes.string.isRequired,
  cost: PropTypes.string,
  unit: PropTypes.string,
  tabs: PropTypes.array.isRequired,
  onClick: PropTypes.func,
  name: PropTypes.string.isRequired,
  buttonSubText: PropTypes.string,
  teaser: PropTypes.object,
};

export default HeaderDescWithTabView;
