/**
 * MotorCompany.js
 * Company List View for Motor
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';

import ChangeInitialFormData from '../Templates/ChangeInitialFormData/ChangeInitialFormData';
import HeaderDescWithTabView from '../Templates/InsurerFormHeader/HeaderDescWithTabView';
import Enquiry from '../Templates/Enquiry/Enquiry';
import MotorEditDetail from './MotorEditDetail';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { ampTag } from '../../lib/tagFunctions';

import './MotorCompany.scss';
import './MotorCommon.scss';

class MotorCompany extends Component {
  constructor(props) {
    super(props);

    this.renderCompany = this.renderCompany.bind(this);
  }

  renderCompany() {
    const {
      plansData,
      setInsurFormState,
      setInsurerIndex,
      history,
    } = this.props;

    const fwd = _.find(plansData, plan => plan.info && plan.info.insurer_slug === 'fwd');
    const newPlansData = fwd ? [fwd] : [];

    _.each(plansData, (plan) => {
      if (plan.info && plan.info.insurer_slug !== 'fwd') {
        newPlansData.push(plan);
      }
    });


    return _.map(newPlansData, (data, index) => {
      let benefits = null;
      let about = null;

      if (data.policy_details) {
        if (data.policy_details.benefits) {
          benefits = (
            <Fragment key={`company-ul-${index}`}>
              <ul>
                {_.reduce(data.policy_details.benefits, (result, benefit, benefitIndex) => {
                  if (benefitIndex > 1) {
                    return result;
                  }
                  const visibleStyle = benefitIndex > 1 ? 'd-none d-sm-flex' : '';
                  const jsx = (
                    <div
                      className={visibleStyle}
                      key={`${data.info.insurer_name}-benefit-${benefitIndex}`}
                    >
                      <li><p>{benefit}</p></li>
                    </div>
                  );
                  result.push(jsx);
                  return result;
                }, [])}
              </ul>
            </Fragment>
          );
        }

        if (data.policy_details.about) {
          about = (
            <Fragment key={`company-about-${index}`}>
              <div className="d-none d-sm-flex">
                <p>{data.policy_details.about}</p>
              </div>
            </Fragment>
          );
        }
      }

      let cost = null;
      let unit = null;
      if (data.policy_info && data.policy_info.classic && data.policy_info.classic.premium) {
        cost = `$${data.policy_info.classic.premium.net_price}`;
        unit = data.policy_info.classic.premium.frequency === 'per annum' ? '/year' : '/month';
      }

      return (
        <HeaderDescWithTabView
          name={`company-tab-${data.info.insurer_name}`}
          key={`${data.info.insurer_name}-tab-${index}`}
          logo={Logos[data.info.insurer_slug]}
          logoDesc={data.info.insurer_name}
          tabs={[
            'Benefits',
            `About ${data.info.insurer_name}`,
          ]}
          cost={cost}
          unit={unit}
          onClick={() => {
            setInsurerIndex(_.findIndex(plansData, plan => plan.info && plan.info.insurer_slug === data.info.insurer_slug));
            setInsurFormState(FORMSTATES.CHOOSEPLAN.index);
            history.push(`/motor${FORMSTATES.CHOOSEPLAN.path}`);
            ampTag('MotorChoosePlanA', {
              partner: data.info.insurer_slug,
            });
          }}
          buttonSubText="Get 5% Cashback"
          teaser={data.info.teaser}
        >
          {_.compact([benefits, about])}
        </HeaderDescWithTabView>
      );
    });
  }

  render() {
    const { motorFields, submitCallback } = this.props;

    return (
      <Fragment>
        <Row className="inner-form-container" noGutters>
          <Col xs={12}>
            <ChangeInitialFormData
              form="motor"
              valuesToListen={[
                { value: 'vehicle_make', desc: 'Brand' },
                { value: 'vehicle_model', desc: 'Model' },
                { value: 'year_registered', desc: 'Registered' },
              ]}
              submitCallback={submitCallback}
            >
              <MotorEditDetail isPopUp motorFields={motorFields} />
            </ChangeInitialFormData>
          </Col>
          {this.renderCompany()}
          <Enquiry />
        </Row>
      </Fragment>
    );
  }
}

MotorCompany.defaultProps = {
};

MotorCompany.propTypes = {
  motorFields: PropTypes.object.isRequired,
  plansData: PropTypes.array.isRequired,
  setInsurFormState: PropTypes.func.isRequired,
  setInsurerIndex: PropTypes.func.isRequired,
  submitCallback: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};


const mapDispatchToProps = dispatch => ({
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(null, mapDispatchToProps)(MotorCompany));
